___CSS_LOADER_EXPORT___.push([module.id, `/* Reset */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: system-ui, sans-serif;
  background-color: #f4f4f4;
  color: #333;
}

/* Header y Footer fijos arriba y abajo */
header, footer {
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
  flex-shrink: 0;
}

/* Main ocupa todo el espacio vertical restante */
main {
  flex: 1;
  width: 100%;
  padding: 2rem 1rem;
}
#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
#wheaterContainer{
  display: flex;
  flex-direction: column;
  object-fit: contain;
  align-content: center;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
}
#wheaterContainer h2{
  align-self: center;
}
/* Responsive: centrado de contenido en pantallas grandes */
@media (min-width: 768px) {
  main {
    max-width: 100%;
    margin: 0 auto;
  }
}


